import React, { Component } from 'react';
import Card from '../card/card';
import Modal from '../modal/modal';
import './modelList.css';

class ModelList extends Component {
    constructor() {
        super();
        this.state = {
            selectedModel: {},
            isModalOpen: false,
        };
    }
    openModelModal(model) {
        this.setState({
            selectedModel: model,
            isModalOpen: true,
        });
    }
    closeModelModal() {
        this.setState({
            selectedModel: {},
            isModalOpen: false
        });
    }
    render() {
        const { selectedModel, isModalOpen } = this.state;
        const lightTypeAr = ["light1", "light2", "bulb1", "bulb2"];
        var thisObject = this;
        // console.log(this.props.models)
        return (
            this.props.models.length > 0 && this.props.isLoaded
                ? (
                    <div className="gallery">
                        <div className="card-list row">

                            {this.props.models.map(function(result){
                                var isPublic = true, control_type = 0, lightType = 'light';
                                result.tags.forEach(function(elem , index){
                                    if (elem.name === 'private' || elem.name === 'Private'){ 
                                        isPublic = false;
                                    } else if (elem.name === 'public' || elem.name === 'Public'){
                                        isPublic = true;
                                    }

                                    if (lightTypeAr.includes(elem.name)){
                                        lightType = elem.name;
                                    }
                                    
                                    if (elem.name === 'remote_white'){
                                        control_type = 1;
                                    } else if (elem.name === 'remote_black'){
                                        control_type = 2;
                                    }
                                    result['controlType'] = control_type;
                                    result['lightType'] = lightType;
                                });
                                return isPublic ? (<Card key={result.uid} model={result} showModelDetails={ thisObject.openModelModal.bind(thisObject) }></Card>) : null;
                            })}
                            {/* {this.props.models.map(result => (
                                <Card key={result.uid} model={result} showModelDetails={ this.openModelModal.bind(this) }></Card>
                            ))} */}
                        </div>
                        <Modal open={isModalOpen} close={this.closeModelModal.bind(this)} 
                        // model={selectedModel.uid}
                        model={selectedModel}
                        ></Modal>
                    </div>
                )
                : !this.props.isLoaded
                    ? (<div className="loading">Loading...</div>)
                    : (<div className="placeholder">No models here.</div>)
        );
    }
}

export default ModelList;