    makeLighter(thisObject){
        thisObject.setState(prevState => ({
            ...prevState,
            fanInfo: {
                ...prevState.fanInfo,
                lightBtnClickedTime: prevState.fanInfo.lightBtnClickedTime + 100,
            }
        }));
        if ( thisObject.state.fanInfo.lightBtnClickedTime > 500 && thisObject.state.fanInfo.lightTurned === true) {

            var api = thisObject.state.fanInfo.api;

            var lightMaterial = {};
            api.getMaterialList(function(err , theMaterialList){

                for ( var i = 0 ; i < theMaterialList.length ; i ++ ){
                    if (theMaterialList[i].name && theMaterialList[i].name == 'Light'){
                        lightMaterial = theMaterialList[i];
                        break;
                    }
                }
                if (!(Object.keys(lightMaterial).length === 0)) {
                    var lighterTime = 30; // make light bright and dimming during 3s.
                    var cu_Cate = thisObject.props.model.lightType, lightMinV , lightMaxV;
                    lightMinV = thisObject.lightTypeVar[cu_Cate].minV;
                    lightMaxV = thisObject.lightTypeVar[cu_Cate].maxV;

                    var matLightStep = (lightMaxV - lightMinV) / lighterTime;
                    var lightStep = (thisObject.lightTypeVar.fanLight.maxV - thisObject.lightTypeVar.fanLight.minV) / lighterTime;

                    var prevVal = lightMaterial.channels['EmitColor']['factor'];
                    if (thisObject.state.fanInfo.lightBtnBrighterToggle === false){
                        lightMaterial.channels['EmitColor']['factor'] = Math.min(prevVal + matLightStep , lightMaxV);
                    } else {
                        lightMaterial.channels['EmitColor']['factor'] = Math.max(prevVal - matLightStep , lightMinV);
                    }
                    console.log(prevVal);

                    api.setMaterial(lightMaterial, function (err) {
                        if (err){
                            console.log('Couldn\'t brighter the light.');
                        } else {
                            // if (thisObject.state.fanInfo.lightBtnBrighterToggle === false){
                            //     api.getLight(0 , function(err , state){
                            //         var prevValue = state.intensity;
                            //         api.setLight(0 , {intensity: Math.min(prevValue + lightStep , thisObject.lightTypeVar.fanLight.maxV)}, function(err) {console.log(err)});
                            //     });
                            // } else {
                            //     api.getLight(0 , function(err , state){
                            //         var prevValue = state.intensity;
                            //         api.setLight(0 , {intensity: Math.max(prevValue - lightStep , thisObject.lightTypeVar.fanLight.minV)}, function(err) {console.log(err)});
                            //     });
                            // }
                        }
                    });
                } else {
                    console.log("Can't Detect Light Object.");
                }
            })
        }
    }

    onLightMouseDown(){
        if (this.state.fanInfo.homeShieldLedToggle === true){
            clearInterval(this.state.fanInfo.changeHomeShieldLight);
            this.setState(prevState => ({
                ...prevState,
                fanInfo: {
                    ...prevState.fanInfo,
                    homeShieldLedToggle: false,
                    lightBtnBrighterToggle: false,
                    changeHomeShieldLight: undefined
                }
            }));
        }

        var interValFunc = this.makeLighter;
        var thisObject = this;
        this.setState(prevState => ({
            ...prevState,
            fanInfo: {
                ...prevState.fanInfo,
                makeLightBrighter: setInterval(function(){ interValFunc(thisObject) }, 100),
                buttonClickedStatus: {
                    ...prevState.fanInfo.buttonClickedStatus,
                    light: true,
                }
            }
        }));
    }
 
    onLightMouseUp(){
        clearInterval(this.state.fanInfo.makeLightBrighter);
        if (this.state.fanInfo.lightBtnClickedTime <= 500) {
            this.handleToggleFanLight();
            this.setState(prevState => ({
                ...prevState,
                fanInfo: {
                    ...prevState.fanInfo,
                    lightBtnClickedTime: 0,
                    makeLightBrighter: undefined,
                    buttonClickedStatus: {
                        ...prevState.fanInfo.buttonClickedStatus,
                        light: false
                    }
                }
            }));
        } else {
            
            this.setState(prevState => ({
                ...prevState,
                fanInfo: {
                    ...prevState.fanInfo,
                    lightBtnClickedTime: 0,
                    makeLightBrighter: undefined,
                    lightBtnBrighterToggle: !prevState.fanInfo.lightBtnBrighterToggle,
                    buttonClickedStatus: {
                        ...prevState.fanInfo.buttonClickedStatus,
                        light: false
                    }
                }
            }));
        }
    }

    toggleFanLight() {
        if (!this.state.fanInfo.api) {
            return;
        }
        var thisObject = this;
        var api = this.state.fanInfo.api;
        var isTurned = this.state.fanInfo.lightTurned;

        var lightMaterial = {};
        api.getMaterialList(function(err , theMaterialList){

            for ( var i = 0 ; i < theMaterialList.length ; i ++ ){
                if (theMaterialList[i].name && theMaterialList[i].name === 'Light'){
                    lightMaterial = theMaterialList[i];
                    break;
                }
            }
            if (!(Object.keys(lightMaterial).length === 0)) {
                if (isTurned){
                    lightMaterial.channels['EmitColor']['enable'] = false;
                } else {
                    lightMaterial.channels['EmitColor']['enable'] = true;
                    lightMaterial.channels['EmitColor']['factor'] = thisObject.lightTypeVar[thisObject.props.model.lightType].minV;
                }

                console.log(lightMaterial)

                api.setMaterial(lightMaterial, function (err) {
                    if (err){
                        console.log('Couldn\'t turn on the light.');
                    } else {
                        // if (isTurned){
                        //     api.setLight(0 , {enabled: false}, function(err) {console.log(err)});
                        // } else {
                        //     api.setLight(0 , {enabled: true, intensity: 0.8}, function(err) {console.log(err)});
                        // }

                        // api.getLight(0 , function (err , light){
                        //     console.log(light);
                        // })

                        thisObject.setState(prevState => ({
                            ...prevState,
                            fanInfo: {
                                ...prevState.fanInfo,
                                lightTurned: !isTurned,
                            }
                        }));
                    }
                });
            } else {
                console.log("Can't Detect Light Object.");
            }
        })
    }

