import React, { Component } from 'react';
import './fanInfo.css';

class FanInfo extends Component {
    constructor(props) {
        super(props);
        console.log(this.props)
    }

    render() {
        return (
            <div className='fan-title'>
                {this.props.name}
            </div>
        );
    }
}

export default FanInfo;